import { mapState, mapActions } from 'vuex'
import { apiUrl, upyunUrl } from '../../../api/config'
import Upyun from '../../../utils/upyun-wxapp-sdk'

const upyun = new Upyun({
  bucket: 'pgsw-flow',
  operator: 'ftpuser',
  getSignatureUrl: `${apiUrl}/msg/get_upyun_sign.php`
})

export default {
  data () {
    return {
      upyunUrl,
      textareaVal: '',
      images: [],
      telVal: '',
      initialStatus: false
    }
  },
  onLoad () {
    this.initialStatus = false
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'addFeedback'
    ]),
    // 选择上传图片
    onChooseImageFn () {
      const _this = this
      uni.chooseImage({
        count: 3,
        sizeType: ['original', 'compressed'],
        sourceType: ['album', 'camera'],
        success (res) {
          const tempFilePaths = res.tempFilePaths
          tempFilePaths.forEach((url) => {
            _this.upyunUploadFn(url)
          })
        }
      })
    },
    // 通过又拍云转网络图片url
    upyunUploadFn (localPath) {
      const _this = this
      _this.showToastIconFn()
      upyun.upload({
        localPath,
        remotePath: '/Pic-x/{year}-{mon}-{day}/{filemd5}{.suffix}',
        success (res) {
          const oJson = JSON.parse(res.data)
          _this.hideToastIconFn()
          if (_this.images.length < 3) {
            _this.images.push(upyunUrl + oJson.url)
          } else {
            _this.showToastFn('亲，最多只能上传3张图片哦!')
          }
        }
      })
    },
    // 移除反馈图片
    onCloseImgFn (index) {
      this.images.splice(index, 1)
    },
    // 提交
    onSubmitFn () {
      if (this.initialStatus) return false
      const images = this.images
      let picUrl = ''
      picUrl = images.join(',')
      const data = {
        that: this,
        textareaVal: this.textareaVal,
        picUrl,
        telVal: this.telVal
      }
      uni.showLoading({ title: 'Loading' })
      this.initialStatus = true
      this.addFeedback(data).then((res) => {
        this.showToastFn(res.message)
        if (res.ret === 1) {
          this.textareaVal = ''
          this.images = []
          this.telVal = ''
          this.initialStatus = false
        }
      })
    },
    // 跳转反馈历史页面
    onSkipPageFn (url) {
      uni.navigateTo({
        url
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    },
    showToastIconFn () {
      uni.showToast({
        icon: 'loading',
        duration: 2000
      })
    },
    hideToastIconFn () {
      uni.hideToast()
    }
  }
}
